        /*Timeline*/
        .timeline-head {
            width: 100%;
            text-align: center;
            margin: 50px 0 0;
        }

        .timeline-head h3 {
            color: var(--winter-w2);
            font-weight: 400;
            font-size: 36px;
            text-decoration: underline;
        }

        .timeline ul {
            padding-top: 40px;
            margin-bottom: 50px;
        }

        .timeline li {
            list-style-type: none;
            position: relative;
            margin: 0 auto;
            max-width: 890px;
            display: flex;
            flex-wrap: wrap;
        }

        .timeline li::before {
            content: " ";
            position: absolute;
            background: var(--winter-w2);
            width: 8px;
            height: 100%;
            left: calc(50% - 4px);
            top: 14px;
        }

        .timeline li::after {
            content: "o";
            position: absolute;
            left: calc(50% - 14px);
            top: 0;
            background: #fff;
            border: solid 8px var(--winter-w2);
            border-radius: 50%;
            height: 28px;
            width: 28px;
            text-indent: -9999px;
        }

        .timeline ul li:last-child::before {
            height: 0;
        }

        .time-responsive {
            display: none;
        }

        time {
            display: flex;
            position: absolute;
            font-size: 20px;
            font-weight: 400;
        }

        .timeline ul li:nth-child(odd) time {
            right: calc(50% + 45px);
        }

        .timeline ul li:nth-child(even) time {
            left: calc(50% + 45px);
            margin-left: auto;
        }

        .timeline-item {
            position: relative;
            transform: translateY(calc(-50% + 13px));
            padding: 24px;
            background: var(--winter-w2);
            color: #fff;
            transition: all 300ms ease-in-out;
            cursor: default;
            border-radius: 2px;
            font-size: 16px;
            font-weight: 400;
            letter-spacing: 0.4px;
        }

        .timeline-item::before {
            content: "";
            position: absolute;
            top: calc(50% - 18px);
            width: 0;
            height: 0;
            border-style: solid;
        }

        .timeline ul li:nth-child(odd) .timeline-item {
            right: calc(-50% - 45px);
        }

        .timeline ul li:nth-child(odd) .timeline-item::before {
            left: -36px;
            border-bottom: 18px solid transparent;
            border-left: 18px solid transparent;
            border-right: 18px solid var(--winter-w2);
            border-top: 18px solid transparent;
        }

        .timeline ul li:nth-child(even) .timeline-item {
            left: calc(-50% - 45px);
            margin-left: auto;
        }

        .timeline ul li:nth-child(even) .timeline-item::before {
            right: -36px;
            border-bottom: 18px solid transparent;
            border-left: 18px solid var(--winter-w2);
            border-right: 18px solid transparent;
            border-top: 18px solid transparent;
        }

        @media screen and (max-width: 900px) {
            .timeline ul li div {
                width: 250px;
            }

            .timeline ul li:nth-child(even) div {
                left: -289px;
            }
        }

        @media screen and (max-width: 600px) {
            time {
                display: none;
            }

            .time-responsive {
                display: block;
            }

            .timeline ul {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }

            .timeline ul li {
                margin-left: 20px;
            }

            .timeline ul li::before, .timeline ul li::after {
                left: unset;
            }

            .timeline ul li div {
                width: calc(100vw - 50px);
                text-align: center;
                padding-left: 40px;
                padding-right: 40px;
            }

            .timeline ul li:nth-child(odd) .timeline-item, .timeline ul li:nth-child(even) .timeline-item {
                left: unset;
                right: unset;
                margin-left: unset;
            }

            .timeline ul li:nth-child(odd) .timeline-item::before, .timeline ul li:nth-child(even) .timeline-item::before {
                left: -36px;
                border-bottom: 18px solid transparent;
                border-left: 18px solid transparent;
                border-right: 18px solid var(--winter-w2);
                border-top: 18px solid transparent;
            }
