            padding: 3rem 7rem 5rem;
        }

        .community h3 {
            text-align: center;
            font-size: 2.3rem;
            text-decoration: underline;
        }

        .go-corner {
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            width: 60px;
            height: 60px;
            overflow: hidden;
            top: 0;
            right: 0;
            background-color: #00838d;
            border-radius: 0 4px 0 32px;
        }

        .go-arrow {
            margin-top: -4px;
            margin-right: -4px;
            color: white;
            font-family: courier, sans;
        }

