:root {
            --winter-heading: #3c4043;
            --winter-background: #f8f9fa;
            --winter-border: #e8eaed;
            --winter-w1: #eceef1;
            --winter-w2: #081b24;
            --winter-w3: #8a9c9b;
            --winter-w4: #acc4d4;
            --winter-w5: #d3e4e3;
            --winter-link: #7ad9fe;
            --winter-hover: rgba(158, 158, 158, 0.2);
            --logo-l1: #f7931e;
            --logo-l2: #0094ff;
            --logo-l3: #8dc500;
        }

        /* Body */
        .body-content {
            color: #081b24;
            color: var(--winter-w2);
            max-width: 1080px;
            margin: 0 auto;
        }

        .cards-bg-sec {
            padding: 2rem 0 7%;
            background: #f8f9fa;
            background: var(--winter-background);
            border-top: 1px solid #e8eaed;
            border-top: 1px solid var(--winter-border);
            -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 2vw));
                    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 2vw));
        }

        .card__card__title {
            color: #3c4043;
            color: var(--winter-heading);
            letter-spacing: -0.005em;
            line-height: 56px;
            font-weight: 300;
            margin-bottom: 40px;
            font-size: 42px;
        }

        .card__card__body {
            padding: 24px;
            background-color: #fff;
            border-radius: 6px;
            box-shadow: 0 0 8px 0 rgba(241, 243, 244, 0.5);
            border: 1px solid #e8eaed;
            border: 1px solid var(--winter-border);
        }

        .card-title {
            font-size: 18px;
            color: #3c4043;
            color: var(--winter-heading);
            font-weight: 600;
            line-height: 28px;
            font-family: "Poppins";
            padding-bottom: 20px;
            border-bottom: 1px solid #e8eaed;
            border-bottom: 1px solid var(--winter-border);
        }

        .card-text {
            margin: 16px 0 20px;
            font-size: 17px;
            font-weight: 400;
            line-height: 25px;
            color: #3c4043;
            color: var(--winter-heading);
        }

        /* header */
        .snow {
            position: absolute;
            width: 100%;
            height: 100%;
        }

        #tsparticles {
            height: 100%;
        }

        .header {
            width: 100%;
            background: #081b24;
            background: var(--winter-w2);
            -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 3vw));
                    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 3vw));
            min-height: 450px;
        }

        .main-header-content {
            justify-content: center;
            color: #fff;
            width: 100%;
            padding: 9rem 7rem 11rem;
        }

        .main-header-content h1 {
            font-family: "Righteous", cursive;
            display: block;
            font-size: 5.5rem;
            align-self: center;
            text-align: center;
        }

        .header-logo {
            width: 100%;
            display: flex;
            justify-content: center;
        }

        .header-logo img {
            width: 260px;
            margin-top: -18px;
        }

        .about-swoc {
            display: flex;
            padding-bottom: 40px;
            justify-content: space-between;
        }

        .about-content {
            padding: 2.5rem 0;
        }

        .about-content h3 {
            letter-spacing: -0.005em;
            line-height: 56px;
            font-weight: 300;
            margin-bottom: 25px;
            font-size: 46px;
        }

        .about-content p {
            font-size: 18px;
            font-weight: 400;
            line-height: 30px;
            color: #3c4043;
            color: var(--winter-heading);
        }

        .about-vector {
            height: 100%;
            margin-top: auto;
        }

        .about-vector img {
            width: 365px;
        }

        .past-numbers {
            display: flex;
            justify-content: space-around;
            align-items: center;
            padding: 100px 0;
            background-color: #f4f4f4;
            /* Light gray background */
        }

        .number-card {
            text-align: center;
            padding: 30px;
            background-color: #fff;
            border-radius: 10px;
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
            transition: box-shadow 0.3s, -webkit-transform 0.3s;
            transition: transform 0.3s, box-shadow 0.3s;
            transition: transform 0.3s, box-shadow 0.3s, -webkit-transform 0.3s;
            cursor: pointer;
        }

        .number-card:hover {
            -webkit-transform: translateY(-5px);
                    transform: translateY(-5px);
            box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
        }

        .number-title {
            font-size: 1.5rem;
            margin-bottom: 10px;
        }

        .countup {
            font-size: 3rem;
            color: #e74c3c;
            font-weight: bold;
        }

        /* Responsive styles for smaller screens */
        @media (max-width: 768px) {
            .past-numbers {
                flex-direction: column;
                padding: 60px 0;
            }

            .number-card {
                margin-bottom: 20px;
            }

            .number-title {
                font-size: 1.2rem;
            }

            .countup {
                font-size: 2rem;
            }
        }

        .card-cover {
            margin-bottom: 20px;
        }

        .to-apply-card {
            max-width: 400px;
            margin: auto;
            text-decoration: none;
            overflow: hidden;
            transition: all 0.3s;
            -webkit-perspective: 1px;
                    perspective: 1px;
            display: flex;
            flex-direction: column;
        }

        .to-apply-card:hover {
            -webkit-transform: scale(1.02);
                    transform: scale(1.02);
        }

        .btn-apply {
            display: flex;
            margin-top: auto;
            margin-bottom: 10px;
        }

        .btn-apply iframe {
            width: 100% !important;
        }

        .btn-apply a {
            border: none;
            outline: none;
            border: none;
            padding: 6px 12px;
            border-radius: 2px;
            color: #fff;
            font-weight: 400;
            width: 100%;
            font-size: 18px;
            background: #3770ff;
            text-align: center;
        }

        .btn-apply a:hover {
            text-decoration: none;
        }

        .disabled {
            opacity: 0.65;
            cursor: no-drop;
            background: #3770ff !important;
        }

        .event-together {
            padding: 40px 0 40px;
        }

        .event-together h3 {
            font-size: 30px;
            font-weight: 300;
            text-align: center;
            margin-bottom: 50px;
            text-transform: uppercase;
            word-spacing: 5px;
        }

        .together-logos {
            display: flex;
            position: relative;
            align-items: center;
            justify-content: space-between;
            width: 70%;
            margin: 0 auto;
        }

        .together-logos span {
            display: block;
            font-family: 'Poppins';
            font-size: 160px;
            line-height: 0;
            -webkit-transform: translateY(45%);
                    transform: translateY(45%);
            z-index: -1;
            opacity: 0.2;
        }

        .logo-swoc, .logo-cwoc {
            width: 40%;
        }

        .logo-swoc img {
            width: 100%;
        }

        .logo-cwoc img {
            width: 100%;
        }

        /*Timeline*/
        .timeline-head {
            width: 100%;
            text-align: center;
            margin: 50px 0 0;
        }

        .timeline-head h3 {
            color: #081b24;
            color: var(--winter-w2);
            font-weight: 400;
            font-size: 36px;
            text-decoration: underline;
        }

        .timeline ul {
            padding-top: 40px;
            margin-bottom: 50px;
        }

        .timeline li {
            list-style-type: none;
            position: relative;
            margin: 0 auto;
            max-width: 890px;
            display: flex;
            flex-wrap: wrap;
        }

        .timeline li::before {
            content: " ";
            position: absolute;
            background: #081b24;
            background: var(--winter-w2);
            width: 8px;
            height: 100%;
            left: calc(50% - 4px);
            top: 14px;
        }

        .timeline li::after {
            content: "o";
            position: absolute;
            left: calc(50% - 14px);
            top: 0;
            background: #fff;
            border: solid 8px #081b24;
            border: solid 8px var(--winter-w2);
            border-radius: 50%;
            height: 28px;
            width: 28px;
            text-indent: -9999px;
        }

        .timeline ul li:last-child::before {
            height: 0;
        }

        .time-responsive {
            display: none;
        }

        time {
            display: flex;
            position: absolute;
            font-size: 20px;
            font-weight: 400;
        }

        .timeline ul li:nth-child(odd) time {
            right: calc(50% + 45px);
        }

        .timeline ul li:nth-child(even) time {
            left: calc(50% + 45px);
            margin-left: auto;
        }

        .timeline-item {
            position: relative;
            -webkit-transform: translateY(calc(-50% + 13px));
                    transform: translateY(calc(-50% + 13px));
            padding: 24px;
            background: #081b24;
            background: var(--winter-w2);
            color: #fff;
            transition: all 300ms ease-in-out;
            cursor: default;
            border-radius: 2px;
            font-size: 16px;
            font-weight: 400;
            letter-spacing: 0.4px;
        }

        .timeline-item::before {
            content: "";
            position: absolute;
            top: calc(50% - 18px);
            width: 0;
            height: 0;
            border-style: solid;
        }

        .timeline ul li:nth-child(odd) .timeline-item {
            right: calc(-50% - 45px);
        }

        .timeline ul li:nth-child(odd) .timeline-item::before {
            left: -36px;
            border-bottom: 18px solid transparent;
            border-left: 18px solid transparent;
            border-right: 18px solid #081b24;
            border-right: 18px solid var(--winter-w2);
            border-top: 18px solid transparent;
        }

        .timeline ul li:nth-child(even) .timeline-item {
            left: calc(-50% - 45px);
            margin-left: auto;
        }

        .timeline ul li:nth-child(even) .timeline-item::before {
            right: -36px;
            border-bottom: 18px solid transparent;
            border-left: 18px solid #081b24;
            border-left: 18px solid var(--winter-w2);
            border-right: 18px solid transparent;
            border-top: 18px solid transparent;
        }

        @media screen and (max-width: 900px) {
            .timeline ul li div {
                width: 250px;
            }

            .timeline ul li:nth-child(even) div {
                left: -289px;
            }
        }

        @media screen and (max-width: 600px) {
            time {
                display: none;
            }

            .time-responsive {
                display: block;
            }

            .timeline ul {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }

            .timeline ul li {
                margin-left: 20px;
            }

            .timeline ul li::before, .timeline ul li::after {
                left: unset;
            }

            .timeline ul li div {
                width: calc(100vw - 50px);
                text-align: center;
                padding-left: 40px;
                padding-right: 40px;
            }

            .timeline ul li:nth-child(odd) .timeline-item, .timeline ul li:nth-child(even) .timeline-item {
                left: unset;
                right: unset;
                margin-left: unset;
            }

            .timeline ul li:nth-child(odd) .timeline-item::before, .timeline ul li:nth-child(even) .timeline-item::before {
                left: -36px;
                border-bottom: 18px solid transparent;
                border-left: 18px solid transparent;
                border-right: 18px solid #081b24;
                border-right: 18px solid var(--winter-w2);
                border-top: 18px solid transparent;
            }
        }

        .overview-section {
            padding: 40px 0 40px;
        }

        .overview-section h3 {
            font-size: 46px;
            font-weight: 300;
        }

        .overview-section p {
            font-size: 18px;
            font-weight: 400;
            line-height: 30px;
            color: #3c4043;
            color: var(--winter-heading);
        }

        .overview-container {
            display: flex;
            justify-content: space-between;
        }

        .overview-vector {
            width: 300px;
        }

        .overview-vector img {
            width: 100%;
        }

        .overview-content {
            width: calc(100% - 380px);
        }

        .carousel-container {
            max-width: 1340px;
            margin: 0 auto;
            cursor: hand;
        }

        .prize-container {
            display: flex;
