@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap");
 @import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap");
 @import url("https://fonts.googleapis.com/css2?family=Righteous&display=swap");
 :root {
	 --winter-w1: #eceef1;
	 --winter-w2: #081b24;
	 --winter-w3: #8a9c9b;
	 --winter-w4: #acc4d4;
	 --winter-w5: #d3e4e3;
	 --winter-link: #7ad9fe;
	 --winter-hover: rgba(158, 158, 158, 0.2);
	 --logo-l1: #f7931e;
	 --logo-l2: #0094ff;
	 --logo-l3: #8dc500;
}
 .main-nav {
	 display: flex;
	 position: fixed;
	 background: unset;
	 width: 100%;
	 padding: 30px 8% 0;
	 z-index: 1000;
	 transition: 0.4s;
	 top: 0;
}
 .win-resize {
	 background: var(--winter-w2);
	 position: relative;
}
 .nav-scrolled {
	 background: var(--winter-w2) !important;
	 padding-top: 10px;
	 padding-bottom: 10px;
	 transition: 0.4s;
	 box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.25);
}
 .nav-logo {
	 float: left;
	 padding: 0.2rem 0;
	 transition: 0.4s;
}
 .nav-logo img {
	 width: 76px;
	 transition: 0.4s;
}
 nav {
	 width: 100%;
}
 .nav-comps {
	 width: 100%;
	 transition: 0.4s;
	 margin: 0 auto;
	 height: 100%;
	 padding: 1rem 0;
}
 .nav-comps ul {
	 list-style: none;
	 display: flex;
	 height: 100%;
	 justify-content: center;
	 transition: 0.2s;
}
 .nav-comps ul li {
	 position: relative;
	 padding: 2px 12px;
	 height: 100%;
	 display: flex;
	 align-items: center;
	 margin-left: 20px;
}
 .nav-comps ul li a {
	 color: #fff;
	 font-size: 0.7rem;
	 font-weight: 400;
	 letter-spacing: 1px;
	 text-transform: uppercase;
	 transition: 0.2s;
}
 .nav-comps ul li a:hover {
	 color: var(--winter-link);
	 text-decoration: none;
}
 .nav-comps ul li a::before {
	 content: "";
	 position: absolute;
	 background: #6bc7b8;
	 height: 0;
	 width: 16px;
	 top: calc(100% - 0px);
	 left: 50%;
	 transform: translateX(-50%);
	 transition: 0.2s;
	 z-index: -100;
}
 .comp-scrolled {
	 width: 42rem;
	 transition: 0.4s;
	 margin-right: 0;
	 padding: 0.5rem;
}
 .comp-scrolled ul li a::before {
	 height: 0;
	 left: 12px;
	 transform: translateX(0);
}
