	 left: -0.2rem;
	 transition: 0.2s;
	 z-index: -100;
}
 */
 .comp-scrolled ul li.nav-active a {
	 color: var(--winter-link);
	 text-decoration: none;
}
 .comp-scrolled ul li.nav-active a:before {
	 border-radius: 0;
	 height: 100%;
	 width: 100%;
	 top: 0;
	 left: 0;
	 background: var(--winter-hover);
}
 .navbar-nav .nav-link {
	 color: white;
	 transition: 0.3s;
}
 .navbar-nav .nav-link:hover {
	 color: rgba(255, 255, 255, 0.7);
}
 .hamburger-icon {
	 color: #fff;
	 font-size: 1.8rem;
}
/* footer */
 .footer-content {
	 background: var(--winter-w2);
	 color: #fff;
	/* clip-path: polygon(100% 100%, 100% 6%, 0% 0%, 0% calc(100% - 0vw));
	 */
	 clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 1.5vw));
}
 .footer-top {
	 position: relative;
	 text-align: right;
	 margin-left: auto;
	 margin-right: auto;
	 max-width: 960px;
	 padding: 5px 15px;
	 width: 100%;
}
 .footer-top button {
	 color: var(--winter-link);
	 font-weight: 400;
	 font-size: 14px;
	 letter-spacing: 0.01em;
	 background: none;
	 border: none;
	 outline: none;
	 padding: 1px 12px 0;
	 margin: 6px 8px;
	 line-height: 36px;
	 min-height: 36px;
	 vertical-align: bottom;
	 transition: 0.3s ease;
	 cursor: pointer;
}
 .footer-top button:hover {
	 background-color: var(--winter-hover);
}
 .footer-top button i {
	 font-size: 21px;
	 vertical-align: middle;
	 margin-left: 5px;
}
 .fa-heart {
	 color: red;
	 font-size: 20px;
}
 .footer-mid {
	 margin: 0 auto;
	 max-width: 960px;
	 padding: 10px 15px 90px;
	 width: 100%;
}
 .footer-mid .f-content {
	 background: var(--winter-w2);
	 color: #fff;
	 display: flex;
	 justify-content: space-between;
}
 .footer-mid .f-content .f-contact {
	 width: 50%;
	 flex-direction: column;
	 display: flex;
}
 .footer-mid .f-content .f-logo {
	 display: flex;
	 width: 40%;
}
 .footer-mid .f-content .f-logo img {
	 width: 100%;
	 margin-top: auto;
}
 .f-nav {
	 display: flex;
}
 .f-nav ul {
	 list-style: none;
	 margin: 0;
	 padding: 0;
	 display: block;
}
 .f-nav ul li {
	 text-decoration: none;
	 text-transform: uppercase;
	 font-weight: 400;
	 letter-spacing: 0.01em;
	 line-height: 1.6em;
	 margin-top: 15px;
}
 .f-nav ul li a {
	 font-size: 16px;
	 color: var(--winter-w4);
}
 .f-nav ul.f-nav-ul {
	 margin-left: 100px;
}
 .footer-copyright-info {
	 padding: 30px 75px 0 0;
	 margin-top: auto;
	 color: #e7e8e9;
}
 .footer-bottom {
	 background: #fff;
	 margin-left: auto;
	 margin-right: auto;
	 max-width: 960px;
	 padding: 0 15px;
	 width: 100%;
}
 .f-bottom-row {
	 max-width: 100vw;
	 padding: 20px 0 15px;
	 display: flex;
	 justify-content: space-between;
}
 .f-bottom {
	 display: flex;
	 padding-top: 5px;
}
 .f-bottom-left p {
	 font-family: 'Poppins';
	 font-size: 18px;
	 font-weight: 600;
	 margin-right: 70px;
}
 .f-bottom-right p {
	 font-size: 14px;
	/* margin-bottom: 0.5rem;
	 */
	 font-family: 'Poppins';
	 font-weight: 400;
}
 .f-media a {
	 color: #666;
	 margin-left: 30px;
	 transition: 0.3s;
}
 .f-media a:nth-of-type(1):hover {
	 color: #3b5999;
}
 .f-media a:nth-of-type(2):hover {
	 color: #55acee;
}
 .f-media a:nth-of-type(3):hover {
	 color: #e4405f;
}
 .f-media a:nth-of-type(4):hover {
	 color: #cd201f;
}
 .f-media a:nth-of-type(5):hover {
	 color: #0077b5;
}
 .f-media i {
	 font-size: 20px;
}
 